/* ----- reset ----- */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
  background: url(../img/bg01.jpg) no-repeat center center / cover;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

html {
	width: 100%;
	height: 100%;
  font-size: 62.5%;
}
body {
  font-family: '游ゴシック', YuGothic, 'メイリオ', Verdana, 'Hiragino Kaku Gothic ProN', Meiryo, sans-serif;
  font-size: 14px;
  font-size: 1.4rem;
  line-height: 1.6;
  letter-spacing: .1em;
  color: #191716;
  background-color: #fff;
}
a {
	text-decoration: none;
	color: #0f949b;
	&:visited, &:hover {
		color: #0f949b;
	}
	&:active {
		color: #16d6e0;
	}
	&:hover {
		text-decoration: underline;
	}
}
/* ----- / reset ----- */

/* mediaquery
------------------------------------------------------------*/

$sp: 480px;  // Smartphone
$tab: 600px; // Tablet
$pc: 960px; // PC
$fpc: 1280px; // Wide PC

@mixin ssp {
	@media screen and ( max-width: ($sp)) {
		/* ===== low480px ===== */
		@content;
	}
}
@mixin sp {
	@media screen and (min-width: ($sp)) and ( max-width: ($tab)) {
		/* ===== 480px〜600px ===== */
		@content;
	}
}
@mixin tab {
	@media screen and (min-width: ($tab)) and ( max-width: ($pc)) {
		/* ===== 600px〜960px ===== */
		@content;
	}
}
@mixin pc {
	@media screen and (min-width: ($pc)) and ( max-width: ($fpc)) {
		/* ===== 960px〜1280px ===== */
		@content;
	}
}
@mixin fpc {
	@media screen and (min-width: ($fpc)) {
		/* ===== over1280px ===== */
		@content;
	}
}


