/* loading
------------------------------------------------------------*/

div#loading-wrapper {
	width: 100%;
	height: 100%;
	display: block;
	position: fixed;
	top: 0;
	background: #555;
	z-index: 9999;
	h2 {
		width: 100%;
    margin-top: -170px;
		margin-left: -60px;
    padding: 10px 0;
    position: absolute;
		top: 0;
    left: 0;
    z-index: 1;
		text-align: center;
		font-weight: bold;
    color: #fff;
    transform: rotate(60deg);

		animation: BLINK 0.5s ease-in-out infinite alternate;
		@keyframes BLINK {
			0% {
				opacity: 1.0;
			}
			100% {
				opacity: 0;
			}
		}
		@include ssp {
			top: 0;
			left: 0;
			z-index: 1;
			color: #fff;
			position: absolute;
			margin-top: -170px;
			transform: rotate(60deg);
			margin-left: -60px;
		}
		@include sp {
			top: 0;
			left: 0;
			z-index: 1;
			color: #fff;
			position: absolute;
			margin-top: -170px;
			transform: rotate(60deg);
			margin-left: -60px;
		}
	}
	div.bg {
		position: relative;
		top: 30vw;
		@include ssp {
			top: 70vw;
			@media screen and (orientation: landscape) {
				top: 45vw;
			}
		}
		@include sp {
			top: 40vw;
		}
	}
}



.centered {
	position: absolute;
	margin: auto;
	left: 0;
	right: 0;
	top: 0;
	bottom: 0;
}
.pour {
	width: 10px;
	height: 180px;
	background-color: #572816;
	border-top-right-radius: 60px;
	/* border-bottom-right-radius: 50px; */
	/* border-bottom-left-radius: 20px; */
	overflow: hidden;
}
.stripes {
	position: relative;
	width: 10px;
	height: 5px;
	background-color: #572816;
	-webkit-transform: rotate(45deg);
	-webkit-animation: pour 0.8s infinite linear;
}
.stripes:nth-child(2) {
	-webkit-animation: pour 0.8s 0.3s infinite linear;
}
.stripes:nth-child(3) {
	-webkit-animation: pour 0.8s 0.6s infinite linear;
}
.splash {
	width: 10px;
	height: 10px;
	background-color: #461b0a;
	border-radius: 50%;
	-webkit-animation: splash-right 0.4s infinite linear;
}
.splash:nth-child(2) {
	-webkit-animation: splash-left 0.4s 0.2s infinite linear;
}
.splash:nth-child(3) {
	-webkit-transform: rotate(-45deg);
	-webkit-animation: splash-high 0.6s 0.1s infinite linear;
}
.pot {
	width: 200px;
	height: 200px;
	left: -270px;
	top: -170px;
	background-color: #444;
	border: 4px solid #444;
	border-top-left-radius: 90px;
	border-top-right-radius: 130px;
	border-bottom-right-radius: 45%;
	border-bottom-left-radius: 45%;
	-webkit-transform: rotate(60deg);
	overflow: hidden;
	-webkit-mask-image: -webkit-radial-gradient(circle, white 100%, black 100%);
}
.pot-handle {
	width: 40px;
  height: 60px;
  top: -385px;
  left: -415px;
  border: black solid 13px;
  border-bottom-left-radius: 50px;
  border-top-left-radius: 50%;
  -webkit-transform: rotate(57deg);
}
.pot-lip {
	width: 40px;
	height: 40px;
	left: -50px;
	top: -190px;
	background-color: black;
	border-top-right-radius: 50%;
	border-bottom-left-radius: 50%;
}
.pot-bar {
	width: 30px;
	height: 120px;
	left: -120px;
	top: -290px;
	background-color: black;
	border-top-right-radius: 5px;
	-webkit-transform: rotate(-30deg);
}
.pot-lines {
	position: absolute;
	margin: auto;
	top: 0;
	bottom: 0;
	left: 30px;
	width: 1px;
	height: 100px;
	background-color: #000;
}
.tick {
	position: relative;
	display: block;
	left: -10px;
	width: 10px;
	height: 1px;
	margin: 10px;
	background-color: #000;
}
.fill {
	position: absolute;
	margin: auto;
	bottom: -50px;
	width: 300px;
	height: 150px;
	background: -webkit-linear-gradient(#572816 20%, #230d03);
	-webkit-transform: rotate(-60deg);
	-webkit-animation: wiggle 0.4s infinite ease-in-out;
}
.coffee {
	width: 140px;
	height: 10px;
	top: 180px;
	background-color: #572816;
	border: 5px solid #444;
	border-radius: 50%;
}
.ripple {
  max-width: 100px;
	margin: auto;
	position: absolute;
	opacity: 0;
	height: 3px;
	top: 0px;
	bottom: 0;
	right: 0;
	left: 0;
	background-color: #461b0a;
	-webkit-animation: ripple 0.55s 0.4s infinite linear;
}
.ripple:nth-child(2) {
	-webkit-animation: ripple 0.55s 0.6s linear;
}
.cup {
	width: 150px;
	height: 80px;
	top: 260px;
	background: -webkit-linear-gradient(#3f3f3f 40%, #000);
	border-bottom-left-radius: 100px;
	border-bottom-right-radius: 100px;
	border-bottom: #000 solid 1px;
}
.cup-bottom {
	width: 50px;
	height: 4px;
	top: 338px;
	background-color: #000;
	border-bottom-left-radius: 50%;
	border-bottom-right-radius: 50%;
}
.cup-handle {
	width: 30px;
	height: 20px;
	top: 250px;
	left: -160px;
	border: #333 solid 10px;
	border-bottom-left-radius: 50px;
	border-top-left-radius: 50%;
}
.plate {
	position: absolute;
	width: 150px;
	height: 20px;
	top: 340px;
	background-color: #333;
	border-radius: 50%;
}
.plate#inner {
	width: 120px;
	height: 15px;
	background: -webkit-radial-gradient(#000, #373737 70%);
}
@-webkit-keyframes pour {
	0% {
		top: 0;
	}
	100% {
		top: 100%;
	}
}
@-webkit-keyframes ripple {
	0% {
		opacity: 1;
		width: 0px;
	}
	100% {
		opacity: 0;
		width: 160px;
	}
}
@-webkit-keyframes splash-right {
	0% {
		width: 0px;
		height: 0px;
		top: 180px;
	}
	60% {
		width: 5px;
		height: 4px;
		top: 150px;
		left: 30px;
	}
	100% {
		width: 6px;
		height: 6px;
		top: 180px;
		left: 50px;
	}
}
@-webkit-keyframes splash-left {
	0% {
		width: 0px;
		height: 0px;
		top: 180px;
	}
	60% {
		width: 5px;
		height: 4px;
		top: 150px;
		left: -30px;
	}
	100% {
		width: 6px;
		height: 6px;
		top: 180px;
		left: -50px;
	}
}
@-webkit-keyframes splash-high {
	0% {
		width: 3px;
		height: 5px;
		top: 180px;
	}
	50% {
		width: 3px;
		height: 5px;
		top: 120px;
	}
	60% {
		width: 2px;
		height: 2px;
		top: 115px;
		left: -20px;
	}
	100% {
		width: 3px;
		height: 3px;
		top: 180px;
		left: -30px;
	}
}
@-webkit-keyframes wiggle {
	0% {
		-webkit-transform: rotate(-62deg);
	}
	50% {
		-webkit-transform: rotate(-58deg);
	}
	100% {
		-webkit-transform: rotate(-62deg);
	}
}

