/*radio_button*/
@mixin custom-input($size, $type) {
  display: none;
  + label {
		width: 5vw;
		height: 5vw;
		padding: 0px 1vw 1.25vw;
		position: relative;
		display: block;
		cursor: pointer;
    &::before {
			content: "";
	    display: block;
	    position: absolute;
			top: 2px;
			left: 2px;
	    width: 5vw;
	    height: 5vw;
	    border: 1px solid #999;
	    border-radius: 50%;
    }
  }
  &:checked + label::before {
		content: "";
	  display: block;
	  position: absolute;
		top: 2px;
		left: 2px;
		width: 5vw;
		height: 5vw;
	  background: #ffa200;
	  border-radius: 50%;
  }
  &:disabled + label {
    opacity: .3;
    cursor: not-allowed;
  }
}
.custom-checkbox {
  @include custom-input(20px, checkbox);
}

